#Login
{
    display: flex;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: white;
    background-image: url("../../media/Arts/login-mobile.jpg");
    background-position: bottom;
    background-size: cover;
    background-repeat: no-repeat;
    padding-top: 10px;
    padding-bottom: 10px;
}

#LoginBox
{
    background-color: rgba(7, 7, 7, 0.644);
    border-radius: 5px;
    width: fit-content;
    margin: auto;
    padding-bottom: 10px;
}

#LoginUserIcon
{
    display: block;
    background-image: url("../../media/UserLogo2.png");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    height: 80px;
}

#LoginPageHeading
{
    display: block;
    text-align: center;
    font-size: 1.5em;
    color: white;
    font-weight: bold;
    margin-top: 10px;
    margin-bottom: 10px;
}

#LoginLog
{
    display: block;
    text-align: center;
    padding-bottom: 5px;
}

.ErrMsgBox
{
    display: inline-block;
    background-color: red;
    width: 250px;
    padding-left: 5px;
    padding-right: 5px;
    border-radius: 10px;
    color: white;
    text-align: center;
}

.ScsMsgBox
{
    display: block;
    background-color: rgb(0, 75, 0);
    width: 250px;
    padding-left: 5px;
    padding-right: 5px;
    border-radius: 10px;
    color: white;
    text-align: center;
}

.InputContainer
{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 10px;
    margin-right: 10px;
}

.FormLabels
{
    font-size: 1em;
    color: rgb(255, 255, 255);
    padding-top: 5px;
    padding-bottom: 5px;
}

.FormInputBox
{
    background-color: white;
    color: black;
    border-radius: 20px;
    border: none;
    outline: none;
    text-align: center;
    width: 250px;
    padding-top: 5px;
    padding-bottom: 5px;
    margin-bottom: 10px;
}

.ButtonHolder
{
    display: block;
    text-align: center;
    width: 100%;
    padding: 5px 0px 5px 0px;
}

.ButtonT1
{
    border: none;
    outline: none;
    text-align: center;
    padding: 5px 10px 5px 10px;
}

#SubmitLogin
{
    font-size: 1em;
    background-image: linear-gradient(to right, #FF512F 0%, #c9045c  51%, #FF512F  100%);
    width: 170px;
    color: rgb(255, 255, 255);
    transition: 0.5s;
    background-size: 200% auto;
    border-radius: 10px;
}

#SubmitLogin:hover {
   background-position: right center; /* change the direction of the change here */
   color: #fff;
   text-decoration: none;
}


#SwitchRegister
{
    display: inline-block;
    font-size: 1em;
    background-image: linear-gradient(to right, #1A2980 0%, #26D0CE  51%, #1A2980  100%);
    width: 150px;
    text-decoration: none;
    color: rgb(255, 255, 255);
    transition: 0.5s;
    background-size: 200% auto;
    border-radius: 10px;
}

 #SwitchRegister:hover {
   background-position: right center; /* change the direction of the change here */
   color: #fff;
   text-decoration: none;
}


@media (prefers-color-scheme: dark) {
    #Login
    {
        background-color: rgba(25, 25, 25);
        background-image: url("../../media/Arts/login-mobile-dark.jpg");
    }
}

@media only screen and (min-width: 768px)
{
    #Login
    {
        background-color: white;
        background-image: url("../../media/Arts/login-desktop.jpg");
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
    }
    #LoginBox
    {
        width: 600px;
    }

    #LoginPageHeading
    {
        font-size: 2em;
    }

    #LoginUserIcon
    {
        height: 100px;
    }

    .InputContainer
    {
        flex-direction: row;
    }

    .FormLabels
    {
        font-size: 1.5em;
        font-weight: 400;
        margin-left: 10px;
    }

    .FormInputBox
    {
        padding: 0px 5px 0px 5px;
        font-size: 1em;
        border-radius: 20px;
        width: 250px;
        height:fit-content;
        padding: 5px 0px 5px 0px;
        margin-right: 10px;
    }

    @media (prefers-color-scheme: dark) {
        #Login
        {
            background-color: rgba(25, 25, 25);
            background-image: url("../../media/Arts/login-desktop-dark.jpg");
        }
    }
}